let timer = '';
import {checkPhone,checkPwd,checkCode} from "@/common/common.js"
export default {
	data() {
		return {
			passwordType: 'password',
			sendMsg:"发送验证码",
			isCanSendCode:true,
			isHidePassword:true,
			registerForm: {
				username: "",
				phone:"",
				password:"",
				passwordTrue:"",
				code:""
			}
		};
	},
	onLoad() {
	},
	methods: {
		//显示密码
		isHidePasswordClick(){
			if (this.passwordType === 'password') {
			    this.passwordType = 'text';
			} else {
			    this.passwordType = 'password';
			}
		},
		//倒计时
		setCodeInterval () {
			//设置验证码重新发送定时器
		    let time = 60;
		    clearInterval(timer);
		    timer = setInterval(() => {
		        time--;
		        this.isCanSendCode=false;
		        this.sendMsg=time + "s"
		        if (time <= 0) {
		            this.isCanSendCode=true;
		            this.sendMsg="重新获取";
		            clearInterval(timer);
		        }
		    }, 1000)
		},
		clearCodeInterval(){
		    clearInterval(timer);
		    this.codemsg="获取验证码";
		},
		//发送验证码
		sendCode () {
			//发送验证码
		    if (!checkPhone(this.phone)) {
		        return
		    }
		    this.setCodeInterval();
			/*
			* 发送验证码逻辑
			*/
		},
		//注册
		submit() {
			if (this.registerForm.phone.length <= 0) {
				uni.showToast({
					icon: 'none',
					title: '请输入手机号'
				});
			    return;
			}
			if (this.registerForm.code.length <= 0) {
				uni.showToast({
					icon: 'none',
					title: '请输入验证码'
				});
			    return;
			}
			if (this.registerForm.password.length <= 0) {
				uni.showToast({
					icon: 'none',
					title: '请输入密码'
				});
			    return;
			}
			if (this.registerForm.passwordTure.length <= 0) {
				uni.showToast({
					icon: 'none',
					title: '请输入确认密码'
				});
			    return;
			}
			if (this.registerForm.passwordTure.length <= 0) {
				uni.showToast({
					icon: 'none',
					title: '请选择角色'
				});
			    return;
			}
		}
	}
}
